            <th>复核人</th>
            <th>作废日期</th>
          </tr>
          <tr
            class="copy-tr"
            v-for="(item, i) in invoice_piao"
            :style="i===selectNum?'background-color: rgb(0, 153, 255);':''"
            :key="i"
            @click="select(i)"
            @dblclick="dbSelectPop(i)"
          >
            <td>
              <input type="text" readonly="readonly" :value="item.type===1?'专用发票':'普通发票'"/>
            </td>
            <td>
              <input type="text" readonly="readonly" :value="item.invoice_code"/>
            </td>
            <td>
              <input type="text" readonly="readonly" :value="item.invoice_Number"/>
            </td>
            <td>
              <input type="text" readonly="readonly" value="0"/>
            </td>
            <!-- 购方姓名 -->
            <td>
              <input type="text" readonly="readonly" :value="item.piao_form.customer_name"/>
            </td>
            <td>
              <input type="text" readonly="readonly" :value="item.piao_form.customer_taxcode"/>
            </td>
            <td>
              <input
                type="text"
                readonly="readonly"
                :value="item.piao_form.customer_forth_textarea"
              />
            </td>
            <td>
              <input
                type="text"
                readonly="readonly"
                :value="item.piao_form.customer_fifth_textarea"
              />
            </td>
            <td>
              <input type="text" readonly="readonly" value="报送状态"/>
            </td>
            <td>
              <input type="text" readonly="readonly" value="报送日志"/>
            </td>
            <td>
              <input type="text" readonly="readonly" value="所属月份"/>
            </td>
            <td>
              <input type="text" readonly="readonly" :value="$store.state.service.serviceData.kaipiao_time"/>
            </td>
            <td>
              <input type="text" readonly="readonly" :value="item.piao_form.invoice_table_data[0].CCP_taxrate"/>
            </td>
            <td>
              <input type="text" readonly="readonly" :value="item.piao_value.shuie"/>
            </td>
            <td>
              <input type="text" readonly="readonly" value="详见销货清单"/>
            </td>
            <td>
              <input type="text" readonly="readonly" value="详见销货清单"/>
            </td>
            <td>
              <input type="text" readonly="readonly" value="管理员"/>
            </td>
            <td>
              <input type="text" readonly="readonly" value/>
            </td>
            <td>
              <input type="text" readonly="readonly" value="是"/>
            </td>
            <td>
              <!-- 作废标志 -->
              <input type="text" readonly="readonly" :value="item.isZuofei?'是':'否'"/>
            </td>
            <td>
              <input type="text" readonly="readonly" :value="item.isQingdan?'是':'否'"/>
            </td>
            <td>
              <input type="text" readonly="readonly" value="无"/>
            </td>
            <td>
              <input type="text" readonly="readonly" value/>
            </td>
            <td>
              <input type="text" readonly="readonly"
                     :value="item.isZuofei?$store.state.service.serviceData.lock_day:''"/>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
      <div id="cancel-notify" class="layui-layer-wrap" style="display: none;">
        <div class="notify-number-cancel">
          <input type="text" id="notify_number_input" value="1张" readonly="readonly"/>
        </div>
        <div>
          <div class="notify-btn-confirm" onclick="click_notify_confirm()"></div>
          <div class="notify-btn-cancel" onclick="click_notify_cancel()"></div>
        </div>
      </div>
    </div>
    <invoicePop v-if="invoice_piao.length > 0" ref="pop" :num="num"></invoicePop>
  </div>
</template>

<script>
  import invoicePop from '@/components/invoice-pop.vue'

  export default {
    components: {
      invoicePop
    },
    data () {
      return {
        selectNum: false,
        num: 0
      }
    },
    computed: {
      invoice_piao: {
        get () {
          return this.$store.getters.getFormContent('invoice_form')
        }
      }
    },
    methods: {
      select (i) {
        this.selectNum = i
      },
      dbSelectPop (i) {
        this.num = i
        this.$refs.pop.private_confirm()
      },
      click_cancel () {
        if (this.selectNum === false) {
          layer.msg('请选择要作废的数据')
          return false
        }
        var _this = this
        layer.confirm('确定要作废本条数据吗？', {
          btn: ['确定', '取消'] //按钮
        }, function () {
          _this.$store.commit('cancleOneData', _this.selectNum)
          layer.msg('作废成功')
          _this.$forceUpdate()
        }, function () {
          layer.msg('未作废')
        })

      },
      click_exit () {
        this.$router.go(-1)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
